.card {
  @media (min-width: $tablet-width) {
    background-color: $color-grey;
  }

  &__link {
    text-decoration: none;
  }

  &__title {
    font-family: $font-heading;
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    text-transform: uppercase;
    color: $color-grey-darker;
  }

  &__parameter {
    font-size: 12px;
  }

}
